@import url(https://fonts.googleapis.com/css?family=Orbitron:500&display=swap);
.calc-button{background-color:white;color:black;border:1px solid black;cursor:pointer;border-radius:5px;display:flex;align-items:center;justify-content:space-around;box-sizing:border-box}.calc-button:hover{color:orange}

#calc-container{background-color:white;box-shadow:0 0 10px 2px;max-width:350px;height:450px;margin:15px auto;padding:20px;border-radius:20px;font-family:'Orbitron';display:grid;grid-template-areas:"display display display display" "clear clear divide multiply" "seven eight nine subtract" "four five six add" "one two three equal" "zero zero decimal equal"}#calc-container #display{grid-area:display;background-color:#e6e6e6;border-radius:10px;margin-bottom:10px;height:70px;text-align:right;padding:5px;word-break:break-all}#calc-container #display #formula{margin:0;padding:0;height:20px;font-size:0.6em;color:orange}#calc-container #display #input-display{padding:0;font-size:0.9em}#calc-container #clear{grid-area:clear;background-color:#eb4747}#calc-container #divide{grid-area:divide;background-color:#70a7c2}#calc-container #multiply{grid-area:multiply;background-color:#70a7c2}#calc-container #subtract{grid-area:subtract;background-color:#70a7c2}#calc-container #add{grid-area:add;background-color:#70a7c2}#calc-container #equals{grid-area:equal;background-color:#52e052}#calc-container #decimal{grid-area:decimal}#calc-container #zero{grid-area:zero}#calc-container #one{grid-area:one}#calc-container #two{grid-area:two}#calc-container #three{grid-area:three}#calc-container #four{grid-area:four}#calc-container #five{grid-area:five}#calc-container #six{grid-area:six}#calc-container #seven{grid-area:seven}#calc-container #eight{grid-area:eight}#calc-container #nine{grid-area:nine}

.icons{font-size:100px;opacity:0.3;z-index:-1;position:relative}.icons #minus-icon{-webkit-animation:rotate-icon;animation:rotate-icon;-webkit-animation-duration:10s;animation-duration:10s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear;left:50px;color:pink;position:absolute}.icons #plus-icon{-webkit-animation:rotate-icon;animation:rotate-icon;-webkit-animation-duration:8s;animation-duration:8s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear;right:70px;top:30px;color:#f6ff7a;position:absolute}.icons #divide-icon{-webkit-animation:rotate-icon;animation:rotate-icon;-webkit-animation-duration:7s;animation-duration:7s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear;left:150px;top:240px;color:#7aa9ff;position:absolute}.icons #equals-icon{-webkit-animation:rotate-icon;animation:rotate-icon;-webkit-animation-duration:9s;animation-duration:9s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear;right:40px;top:320px;color:#7affa2;position:absolute}.icons #not-equals-icon{-webkit-animation:rotate-icon;animation:rotate-icon;-webkit-animation-duration:9.5s;animation-duration:9.5s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear;right:430px;top:-30px;color:#7affe9;position:absolute}.icons #wave-square-icon{-webkit-animation:rotate-icon;animation:rotate-icon;-webkit-animation-duration:8.5s;animation-duration:8.5s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear;right:265px;top:200px;color:#bf7aff;position:absolute}.icons #sqrt-icon{-webkit-animation:rotate-icon;animation:rotate-icon;-webkit-animation-duration:8.5s;animation-duration:8.5s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear;left:365px;top:50px;color:#ff7a7a;position:absolute}.icons #gt-icon{-webkit-animation:rotate-icon;animation:rotate-icon;-webkit-animation-duration:8s;animation-duration:8s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear;right:365px;top:400px;color:#ffd17a;position:absolute}.icons #lt-icon{-webkit-animation:rotate-icon;animation:rotate-icon;-webkit-animation-duration:9.5s;animation-duration:9.5s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear;left:465px;top:450px;color:#ff7ac8;position:absolute}.icons #subscript-icon{-webkit-animation:rotate-icon;animation:rotate-icon;-webkit-animation-duration:10s;animation-duration:10s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear;left:-10px;top:470px;color:#ff997a;position:absolute}@-webkit-keyframes rotate-icon{0%{-webkit-transform:rotate(0);transform:rotate(0)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes rotate-icon{0%{-webkit-transform:rotate(0);transform:rotate(0)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

